section.Loading {
  text-align: center; }
  section.Loading.center {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%); }
  section.Loading .text {
    margin-top: 10px; }

a.RoundButton {
  position: absolute;
  right: 20px;
  top: 20px;
  color: white;
  border: 3px solid white;
  border-radius: 18px;
  padding: 8px;
  text-decoration: none;
  font-weight: bold;
  min-width: 80px;
  text-align: center; }
  a.RoundButton:hover {
    background: rgba(255, 255, 255, 0.178); }
  a.RoundButton + a.RoundButton {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    margin-right: 10px; }
  a.RoundButton svg {
    margin-right: 6px; }

.PageNotFound {
  padding: 20px;
  background: #ce5454;
  color: white; }


section.GridLayout {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20px, 1fr)); }
  section.GridLayout > .GridTile {
    box-sizing: border-box; }

iframe.ui-iframe {
  border: none;
  height: 100%;
  width: 100%; }




section.Welcome {
  padding: 20px;
  background-color: #000;
  color: white;
  text-align: center; }
  section.Welcome .title {
    font-size: 1.5em;
    margin: 1.5em; }
  section.Welcome .logo {
    -webkit-animation: Welcome-logo-spin infinite 20s linear;
            animation: Welcome-logo-spin infinite 20s linear;
    height: 80px; }

@-webkit-keyframes Welcome-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes Welcome-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

section.ComponentNotFound {
  padding: 20px;
  text-align: center;
  background: #ce5454;
  color: white; }
  section.ComponentNotFound .text {
    margin-top: 10px; }
    section.ComponentNotFound .text span + span {
      margin-left: 6px; }

section.WebComponent {
  position: relative;
  height: 100%;
  width: 100%; }

section.App {
  position: absolute;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0; }
  section.App section.WebPage {
    width: 100%;
    height: 100%;
    overflow: auto; }

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

